/*--------------------------------------------------------------
# Sections
--------------------------------------------------------------*/
section {
  overflow: hidden;
}

#test {
  display: flex;
  justify-content: center;
  align-items: center;
  /* min-height: 20vh; */
  /* background-color: #000; */
}

.test-content {
  position: relative;
}

.test-content h1 {
  position: absolute;
  top: 18px;
  left: 115px;
  font-size: 2rem;
  transform: translate(-50%, -50%);
  /* color: #000; */
  font-weight: bolder;
}

.test-content h1:nth-child(1) {
  color: transparent;
  -webkit-text-stroke: 0.5px #03a9f4;
}

.test-content h1:nth-child(2) {
  color: #03a9f4;
  animation: animate 4s ease-in-out infinite;
}

@keyframes animate {
  0%, 100%
  {
    clip-path: polygon(0% 45%, 15% 44%, 32% 50%, 54% 60%, 70% 61%, 84% 59%, 100% 52%, 100% 100%, 0% 100%);
  }
  50% {
    clip-path: polygon(0% 60%, 16% 65%, 34% 66%, 51% 62%, 67% 50%, 84% 45%, 100% 46%, 100% 100%, 0% 100%);
  }
}


